	import utils from '@/utils/utils.js';
export default {

	data() {
		return {
			password:"",
			passwordd:""
			
		}
	},
	methods: {
		updatesave(){
			if(this.password==''){
				uni.showToast({
					title:"请输入密码"
				})
				return;
			}
			if(!utils.checkMobilee(this.password) ){
				uni.showToast({
					title:"密码只能6-20位",
				})
				return;
			}
			if(this.passwordd==''){
				uni.showToast({
					title:"请再次输入密码"
				})
					return;
			}
			if(this.password != this.passwordd){
				uni.showToast({
					title:"两次密码输入不一致"
				})
					return;
			}
			if(this.password == this.passwordd){
				uni.showToast({
					title:"修改成功",
				})
				uni.reLaunch({
					url:"../login/login",
				})
					return;
			}
		},
		
	}
}
